earance */\n\n/* stylelint-disable property-no-unknown */\n\n:export {\n  nav-timing-in: 250;\n  nav-items-delay-in: 800;\n  nav-pattern-delay-in: 600;\n  nav-item-stagger: 90;\n  nav-anim-out: 150;\n}\n\n/* stylelint-enable property-no-unknown */\n\n.root {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 5;\n  /*\r\n    &::after,\r\n    &::before {\r\n        position: absolute;\r\n        top: 0;\r\n        left: 0;\r\n        z-index: z-index(behind);\r\n        visibility: hidden;\r\n        overflow: hidden;\r\n        width: 1px;\r\n        height: 1px;\r\n    }\r\n    */\n}\n\n.burger {\n  position: absolute;\n  top: 20px;\n  right: 25px;\n  z-index: 2;\n  opacity: 0;\n  transform: translateX(100%);\n  transition: all 0.3s ease-out;\n}\n\n@media (min-width: 64em) {\n  .burger {\n    top: 30px;\n    right: 30px;\n  }\n}\n\n.root.is-visible .burger {\n  opacity: 1;\n  transform: translateX(0);\n  transition-delay: 1.7s;\n}\n\n.menu {\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: 1;\n  display: flex !important;\n  visibility: hidden;\n  overflow: hidden;\n  flex-direction: column;\n  width: 100%;\n  height: 100%;\n  margin: 0;\n  padding: 0 0 2vh;\n  list-style: none;\n  justify-content: center;\n  align-items: stretch;\n  background-color: transparent;\n  text-align: center;\n  pointer-events: none;\n  transform: translateX(100%);\n  transform-origin: 0 50%;\n}\n\n@media (min-width: 64em) {\n  .menu {\n    padding: 10vh 0 0;\n  }\n}\n\n.menu::after {\n  position: absolute;\n  top: 50%;\n  right: 0;\n  left: 0;\n  z-index: -1;\n  display: block;\n  border-bottom: 1px solid #a02c2c;\n  background-color: #a02c2c;\n  background-image: linear-gradient(55deg, #a02c2c 40%, rgba(160, 44, 44, 0.2) 100%), url(\"./../../../images/circuits-full.svg\");\n  background-position: right 0;\n  content: \"\";\n}\n\n.menu::before {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: -2;\n  background-color: #a02c2c;\n