oint-min: 1280px;\r\n    $breakpoint-max: 2560px;\r\n    //$breakpoint-max: 1920px;\r\n    $breakpoint-diff: ($breakpoint-max - $breakpoint-min);\r\n    $font-scale: $font-diff / $breakpoint-diff;\r\n    */\r\n    $base-font-size: percentage($font-size-default / 16px);\r\n    font-size: $base-font-size;\r\n\r\n    @include mq(tablet-landscape) {\r\n        @include responsive-typography(\r\n            $font-min: 14px,\r\n            $font-max: $font-size-default,\r\n            $breakpoint-min: 1024px,\r\n            $breakpoint-max: 1280px\r\n        );\r\n    }\r\n\r\n    @include mq(desktop) {\r\n        @include responsive-typography(\r\n            $font-min: $font-size-default,\r\n            $font-max: 20px,\r\n            $breakpoint-min: 1280px,\r\n            $breakpoint-max: 2560px\r\n        );\r\n    }\r\n}\r\n\r\nbody {\r\n    overflow: hidden;\r\n    min-width: 320px;\r\n    height: 100vh;\r\n    margin: 0;\r\n    padding: 0;\r\n    background: $color-bg;\r\n    color: $color-text;\r\n    -moz-osx-font-smoothing: grayscale;\r\n    -webkit-font-smoothing: antialiased;\r\n    font-family: $font-family-base;\r\n    //overflow-x: hidden;\r\n    line-height: $font-lheight-default / $font-size-default;\r\n}\r\n\r\n//Remove text-shadow in selection highlight: h5bp.com/i\r\n//These selection declarations have to be separate.\r\n//Customize the background color to match your design.\r\n\r\n::-moz-selection {\r\n    background-color: rgba($color-link, 0.2);\r\n    color: darken($color-link, 15%);\r\n    text-shadow: none;\r\n}\r\n\r\n::selection {\r\n    background-color: rgba($color-link, 0.2);\r\n    color: darken($color-link, 15%);\r\n    text-shadow: none;\r\n}\r\n\r\n/*\r\n::-webkit-scrollbar {\r\n    width: 8px;\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n    background: darken($color-link, 10%);\r\n}\r\n\r\n::-webkit-scrollbar-track {\r\n    background: $color-link;\r\n}\r\n*/\r\n// Remove the gap between images and the bottom of their containers: h5bp.com/i/440\r\n\r\naudio,\r\ncanvas,\r\niframe,\r\nsvg,\r\nvideo,\r\nimg {\r\n    vertical-align: middle;\r\n}\r\n\r\n// A better looking default horizontal rule\r\n\r\nhr {\r\n    display: block;\r\n    height: 1px;\r\n    margin: 1rem 0;\r\n    padding: 0;\r\n    border: 0;\r\n    border-top: 1px solid $color-border;\r\n}\r\n\r\n// Remove default fieldset styles.\r\n\r\nfieldset {\r\n    margin: 0;\r\n    padding: 0;\r\n    border: 0;\r\n}\r\n\r\n// Allow only vertical resizing of textareas.\r\n\r\ntextarea {\r\n    resize: vertical;\r\n}\r\n\r\ninput::-moz-focus-inner {\r\n    border: 0;\r\n}\r\n\r\n\r\n/* ==========================================================================\r\n   ##prevent 300ms delay in FF, Chrome and IE on touch devices\r\n   ========================================================================== */\r\n\r\na[href],\r\nbutton {\r\n    touch-action: manipulation;\r\n}\r\n\r\n/* ==========================================================================\r\n   ##Headings and Base\r\n\r\n   1 to 6 heading\r\n\r\n   ```\r\n   ```\r\n   ========================================================================== */\r\n\r\n@each $font-head-num, $font-head-size in $font-sizes-headings {\r\n\r\n    h#{$font-head-num},\r\n    :global(.h#{$font-head-num}) {\r\n        margin: 0 0 0.2em;\r\n        //color: $color-text\r\n        font-family: $font-family-h;\r\n        font-size: font-size($font-head-size);\r\n        font-weight: font-weight(regular);\r\n        line-height: 1.2;\r\n    }\r\n}\r\n\r\np {\r\n    margin: 0 0 0.2em;\r\n    line-height: 1.2;\r\n}\r\n\r\nabbr[title] {\r\n    border-bottom: 0;\r\n\r\n    :global(.no-touchevents) & {\r\n        border-bottom: 1px dotted currentColor;\r\n        text-decoration: none;\r\n\r\n        @include mq($media-type: print) {\r\n            border-bottom: 0;\r\n        }\r\n    }\r\n}\r\n\r\n:global(a.u-link--mark),\r\n:global(a:not([class])) {\r\n    background-image:\r\n        linear-gradient(\r\n            to top,\r\n            rgba($color-text, 0.2) 0,\r\n            rgba($color-text, 0.2) 3px,\r\n            transparent 4px\r\n        );\r\n    color: inherit;\r\n    outline: 0;\r\n    text-decoration: none;\r\n}\r\n\r\n:global(.no-touchevents a.u-link--mark),\r\n:global(.no-touchevents a:not([class])) {\r\n    position: relative;\r\n    display: inline-block;\r\n    padding-right: 0.22em;\r\n    padding-left: 0.22em;\r\n    background-image: none;\r\n    color: inherit;\r\n    outline: 0;\r\n    text-decoration: none;\r\n    transition: all 0.27s;\r\n\r\n\r\n    &::after {\r\n        position: absolute;\r\n        right: 0;\r\n        bottom: -0.17em;\r\n        left: 0;\r\n        z-index: z-index(behind) - 1;\r\n        opacity: 0.1;\r\n        height: 3px;\r\n        background-color: currentColor;\r\n        content: \"\";\r\n        pointer-events: none;\r\n        transition: all 0.1s linear;\r\n    }\r\n\r\n    &::before {\r\n        position: absolute;\r\n        top: 0;\r\n        bottom: -0.17em;\r\n        left: 0;\r\n        z-index: z-index(above);\r\n        opacity: 0;\r\n        width: 0;\r\n        background-color: $color-link;\r\n        content: \"\";\r\n        pointer-events: none;\r\n        transform: scaleX(1);\r\n        transform-origin: 0 0;\r\n        transition: opacity 0.25s, width 0s ease 0.25s, transform 0s ease 0.25s, transform-origin 0s ease 0.25s;\r\n    }\r\n\r\n    &:hover {\r\n        color: $color-link;\r\n        text-decoration: none;\r\n        transition-delay: 0.25s;\r\n        transition-duration: 0;\r\n\r\n        &::before {\r\n            opacity: 1;\r\n            width: 100%;\r\n            transform: scaleX(0);\r\n            transform-origin: right 0;\r\n            transition: width 0.25s ease, transform-origin 0s ease 0.25s, transform 0.25s ease 0.25s;\r\n        }\r\n\r\n        &::after {\r\n            opacity: 0.3;\r\n            transition: opacity 0.15s ease 0.15s;\r\n        }\r\n    }\r\n}\r\n\