he target width for the fixed-width layout\n/// (i.e. when $mq-responsive is set to 'false') in a old-ie.scss\n///\n/// @example scss\n///  // tablet-only.scss\n///  //\n///  // Ignore all styles above tablet breakpoint,\n///  // and fix the styles (e.g. layout) at tablet width\n///  $mq-responsive: false;\n///  $mq-static-breakpoint: tablet;\n///  @import 'main'; // @media queries in this file will be rasterized up to tablet\n///                   // larger breakpoints will be ignored\n///\n/// @type String\n/// @link https://github.com/sass-mq/sass-mq#adding-custom-breakpoints Full documentation and examples\n$mq-static-breakpoint: desktop !default;\n\n/// Show breakpoints in the top right corner\n///\n/// If you want to display the currently active breakpoint in the top\n/// right corner of your site during development, add the breakpoints\n/// to this list, ordered by width, e.g. (mobile, tablet, desktop).\n///\n/// @type map\n$mq-show-breakpoints: () !default;\n\n/// Customize the media type (e.g. `@media screen` or `@media print`)\n/// By default sass-mq uses an \"all\" media type (`@media all and â¦`)\n///\n/// @type String\n/// @link https://github.com/sass-mq/sass-mq#changing-media-type Full documentation and examples\n$mq-media-type: all !default;\n\n/// Convert pixels to ems\n///\n/// @param {Number} $px - value to convert\n/// @param {Number} $base-font-size ($mq-base-font-size) - `<body>` font size\n///\n/// @example scss\n///  $font-size-in-ems: mq-px2em(16px);\n///  p { font-size: mq-px2em(16px); }\n///\n/// @requires $mq-base-font-size\n/// @returns {Number}\n@function mq-px2em($px, $base-font-size: $mq-base-font-size) {\n    @if unitless($px) {\n        @warn \"Assuming #{$px} to be in pixels, attempting to convert it into pixels.\";\n        @return mq-px2em($px * 1px, $base-font-size);\n    } @else if unit($px) == em {\n        @return $px;\n    }\n    @return ($px / $base-font-size) * 1em;\n}\n\n/// Get a breakpoint's width\n///\n/// @param {String} $name - Name of the breakpoint. One of $mq-breakpoints\n///\n/// @example scss\n///  $tablet-width: mq-get-breakpoint-width(tablet);\n///  @media (min-width: mq-get-breakpoint-width(desktop)) {}\n///\n/// @requires {Variable} $mq-breakpoints\n///\n/// @returns {Number} Value in pixels\n@function mq-get-breakpoint-width($name, $breakpoints: $mq-breakpoints) {\n    @if map-has-key($breakpoints, $name) {\n        @return map-get($breakpoints, $name);\n    } @else {\n        @warn \"Breakpoint #{$name} wasn't found in $breakpoints.\";\n    }\n}\n\n/// Media Query mixin\n///\n/// @param {String | Boolean} $from (false) - One of $mq-breakpoints\n/// @param {String | Boolean} $until (false) - One of $mq-breakpoints\n/// @param {String | Boolean} $and (false) - Additional media query parameters\n/// @param {String} $media-type ($mq-media-type) - Media type: screen, printâ¦\n///\n/// @ignore Undocumented API, for advanced use only:\n/// @ignore @param {Map} $breakpoints ($mq-breakpoints)\n/// @ignore @param {String} $static-breakpoint ($mq-static-breakpoint)\n///\n/// @content styling rules, wrapped into a @media query when $responsive is true\n///\n/// @requires {Variable} $mq-media-type\n/// @requires {Variable} $mq-breakpoints\n/// @requires {Variable} $mq-static-breakpoint\n/// @requires {function} mq-px2em\n/// @requires {function} mq-get-breakpoint-width\n///\n/// @link https://github.com/sass-mq/sass-mq#responsive-mode-on-default Full documentation and examples\n///\n/// @example scss\n///  .element {\n///    @include mq($from: mobile) {\n///      color: red;\n///    }\n///    @include mq($until: tablet) {\n///      color: blue;\n///    }\n///    @include mq(mobile, tablet) {\n///      color: green;\n///    }\n///    @include mq($from: tablet, $and: '(orientation: landscape)') {\n///      color: teal;\n///    }\n///    @include mq(950px) {\n///      color: hotpink;\n///    }\n///    @include mq(tablet, $media-type: screen) {\n///      color: hotpink;\n///    }\n///    // Advanced use:\n///    $my-breakpoints: (L: 900px, XL: 1200px);\n///    @include mq(L, $breakpoints: $my-breakpoints, $static-breakpoint: L) {\n///      color: hotpink;\n///    }\n///  }\n@mixin mq(\n    $from: false,\n    $until: false,\n    $and: false,\n    $media-type: $mq-media-type,\n    $breakpoints: $mq-breakpoints,\n    $responsive: $mq-responsive,\n    $static-breakpoint: $mq-static-breakpoint\n) {\n    $min-width: 0;\n    $max-width: 0;\n    $media-query: '';\n\n    // From: this breakpoint (inclusive)\n    @if $from {\n        @if type-of($from) == number {\n            $min-width: mq-px2em($from);\n        } @else {\n            $min-width: mq-px2em(mq-get-breakpoint-width($from, $breakpoints));\n        }\n    }\n\n    // Until: that breakpoint (exclusive)\n    @if $until {\n        @if type-of($until) == number {\n            $max-width: mq-px2em($until);\n        } @else {\n            $max-width: mq-px2em(mq-get-breakpoint-width($until, $breakpoints)) - .01em;\n        }\n    }\n\n    // Responsive support is disabled, rasterize the output outside @media blocks\n    // The browser will rely on the cascade itself.\n    @if $responsive == false {\n        $static-breakpoint-width: mq-get-breakpoint-width($static-breakpoint, $breakpoints);\n        $target-width: mq-px2em($static-breakpoint-width);\n\n        // Output only rules that start at or span our target width\n        @if (\n            $and == false\n            and $min-width <= $target-width\n            and (\n                $until == false or $max-width >= $target-width\n            )\n        ) {\n            @content;\n        }\n    }\n\n    // Responsive support is enabled, output rules inside @media queries\n    @else {\n        @if $min-width != 0 { $media-query: '#{$media-query} and (min-width: #{$min-width})'; }\n        @if $max-width != 0 { $media-query: '#{$media-query} and (max-width: #{$max-width})'; }\n        @if $and            { $media-query: '#{$media-query} and #{$and}'; }\n\n        // Remove unnecessary media query prefix 'all and '\n        @if ($media-type == 'all' and $media-query != '') {\n            $media-type: '';\n            $media-query: str-slice(unquote($media-query), 6);\n        }\n\n        @media #{$media-type + $media-query} {\n            @content;\n        }\n    }\n}\n\n/// Add a breakpoint\n///\n/// @param {String} $name - Name of the breakpoint\n/// @param {Number} $width - Width of the breakpoint\n///\n/// @requires {Variable} $mq-breakpoints\n///\n/// @example scss\n///  @include mq-add-breakpoint(tvscreen, 1920px);\n///  @include mq(tvscreen) {}\n@mixin mq-add-breakpoint($name, $width) {\n    $new-breakpoint: ($name: $width);\n    $mq-breakpoints: map-merge($mq-breakpoints, $new-breakpoint) !global;\n}\n\n/// Show the active breakpoint in the top right corner of the viewport\n/// @link https://github.com/sass-mq/sass-mq#seeing-the-currently-active-breakpoint\n///\n/// @param {List} $show-breakpoints ($mq-show-breakpoints) - List of breakpoints to show in the top right corner\n/// @param {Map} $breakpoints ($mq-breakpoints) - Breakpoint names and sizes\n///\n/// @requires {Variable} $mq-breakpoints\n/// @requires {Variable} $mq-show-breakpoints\n///\n/// @example scss\n///  // Show breakpoints using global settings\n///  @include mq-show-breakpoints;\n///\n///  // Show breakpoints using custom settings\n///  @include mq-show-breakpoints((L, XL), (S: 300px, L: 800px, XL: 1200px));\n@mixin mq-show-breakpoints($show-breakpoints: $mq-show-breakpoints, $breakpoints: $mq-breakpoints) {\n    body:before {\n        background-color: #FCF8E3;\n        border-bottom: 1px solid #FBEED5;\n        border-left: 1px solid #FBEED5;\n        color: #C09853;\n        font: small-caption;\n        padding: 3px 6px;\n        pointer-events: none;\n        position: fixed;\n        right: 0;\n        top: 0;\n        z-index: 100;\n\n        // Loop through the breakpoints that should be shown\n        @each $show-breakpoint in $show-breakpoints {\n            $width: mq-get-breakpoint-width($show-breakpoint, $breakpoints);\n            @include mq($show-breakpoint, $breakpoints: $breakpoints) {\n                content: \"#{$show-breakpoint} â¥ #{$width} (#{mq-px2em($width)})\";\n            }\n        }\n    }\n}\n\n@if length($mq-show-breakpoints) > 0 {\n    @include mq-show-breakpoints;\n}\n","// ==========================================================================\r\n// Mixins and functions\r\n//\r\n// Note: always put function at the beginning of the sub-section\r\n// ==========================================================================\r\n\r\n@import \"globals\";\r\n@import \"fonts\";\r\n@import \"sass-mq/mq\";\r\n\r\n// Generic\r\n// ==========================================================================\r\n\r\n@function perc($width, $container-width) {\r\n    @return percentage($width / $container-width);\r\n}\r\n\r\n@function strip-units($value) {\r\n    @return $value / ($value * 0 + 1);\r\n}\r\n\r\n@function z-index($level: 'base') {\r\n    @return map-get($layers, $level);\r\n}\r\n\r\n@function vw($width, $viewport-reference-width: $viewport-width) {\r\n    @return (strip-units($width) / strip-units($viewport-reference-width)) * 100 + vw;\r\n}\r\n\r\n@function vh($height, $viewport-reference-height: $viewport-height) {\r\n    @return (strip-units($height) / strip-units($viewport-reference-height)) * 100 + vh;\r\n}\r\n\r\n\r\n// Elements\r\n// ==========================================================================\r\n\r\n@mixin boost-performance ($translate: true) {\r\n    backface-visibility: hidden;\r\n    transform-style: preserve-3d;\r\n    @if ($translate) {\r\n        transform: translate3d(0, 0, 0);\r\n        will-change: transform;\r\n    }\r\n}\r\n\r\n@mixin clearfix {\r\n\r\n    &::before,\r\n    &::after {\r\n        display: table;\r\n        content: \" \";\r\n    }\r\n\r\n    &::after {\r\n        clear: both;\r\n    }\r\n}\r\n\r\n@mixin img-responsive($display: block) {\r\n    display: $display;\r\n    max-width: 100%;\r\n    height: auto;\r\n}\r\n\r\n//see https://github.com/tinganho/compass-placeholder\r\n@mixin placeholder {\r\n\r\n    &::-webkit-input-placeholder {\r\n        @content;\r\n    }\r\n\r\n    &:-moz-placeholder {\r\n        @content;\r\n    }\r\n\r\n    &::-moz-placeholder {\r\n        @content;\r\n    }\r\n\r\n    &:-ms-input-placeholder {\r\n        @content;\r\n    }\r\n}\r\n\r\n@mixin classed-headings($headings...) {\r\n    @if length($headings) == 0 {\r\n        $headings: map-keys($font-sizes-headings);\r\n    }\r\n    $selectors: ();\r\n    @each $i in $headings {\r\n        $current-heading: nth($headings, $i);\r\n        $selector: unquote(\"h#{$current-heading}, .h#{$current-heading}\");\r\n        $selectors: append($selectors, $selector, comma);\r\n    }\r\n\r\n    #{$selectors} {\r\n        @content;\r\n    }\r\n}\r\n\r\n\r\n\r\n// Typography\r\n//\r\n// ==========================================================================\r\n\r\n@function em($pixels, $context: $font-size-default) {\r\n    @if (unitless($pixels)) {\r\n        $pixels: $pixels * 1px;\r\n    }\r\n\r\n    @if (unitless($context)) {\r\n        $context: $context * 1px;\r\n    }\r\n\r\n    @return #{$pixels / $context}em;\r\n}\r\n\r\n@function rem($pixels) {\r\n    @if (unitless($pixels)) {\r\n        $pixels: $pixels * 1px;\r\n    }\r\n    @return #{$pixels / $font-size-default}rem;\r\n}\r\n\r\n\r\n@function font-size($size, $rem: true) {\r\n    $result: $size;\r\n    @if map-has-key($font-sizes, $size) {\r\n        $result: map-get($font-sizes, $size);\r\n    }\r\n\r\n    @if $rem == true {\r\n        @return rem($result);\r\n    }\r\n\r\n    @return $result;\r\n}\r\n\r\n@function font-weight($weight) {\r\n    @if map-has-key($font-weights, $weight) {\r\n        @return map-get($font-weights, $weight);\r\n    } @else {\r\n        @warn \"Weight #{$weight} undefined\";\r\n    }\r\n}\r\n\r\n@function line-height($size, $context: m) {\r\n    $context-size: font-size($context, false);\r\n    @return em($size, $context-size);\r\n}\r\n\r\n\r\n@mixin text-ellipsis {\r\n    overflow: hidden;\r\n    white-space: nowrap;\r\n    text-overflow: ellipsis;\r\n}\r\n\r\n@mixin visually-hidden {\r\n    position: absolute;\r\n    overflow: hidden;\r\n    clip: rect(0 0 0 0);\r\n    width: 1px;\r\n    height: 1px;\r\n    margin: -1px;\r\n    padding: 0;\r\n    border: 0;\r\n}\r\n\r\n@mixin font-heading() {\r\n    margin-bottom: 0.7em;\r\n    font-family: $font-family-sans;\r\n    font-size: font-size(20px);\r\n    font-weight: font-weight(bold);\r\n\r\n    @include mq(tablet-landscape) {\r\n        font-size: font-size(22px);\r\n    }\r\n}\r\n\r\n@mixin font-content() {\r\n    font-size: font-size(16px);\r\n    font-weight: font-weight(regular);\r\n\r\n    @include mq(tablet-landscape) {\r\n        font-size: font-size(18px);\r\n    }\r\n}\r\n\r\n@mixin responsive-typography($font-min, $font-max, $breakpoint-min, $breakpoint-max) {\r\n    //https://www.smashingmagazine.com/2016/05/fluid-typography/\r\n\r\n    $font-diff: ($font-max - $font-min);\r\n    $breakpoint-diff: ($breakpoint-max - $breakpoint-min);\r\n    $font-scale: $font-diff / $breakpoint-diff;\r\n    $base-font-size: percentage($font-min / 16px);\r\n    $base-font-size-em: em($font-min, 16px);\r\n\r\n    // https://zellwk.com/blog/viewport-based-typography/\r\n    font-size: calc(#{$base-font-size-em} + (#{strip-units($font-diff)} * (100vw - #{$breakpoint-min}) / #{strip-units($breakpoint-diff)}));\r\n    //font-size: calc(#{$base-font-size} + (#{strip-units($font-diff)} * (100vw - #{$breakpoint-min}) / #{strip-units($breakpoint-diff)}));\r\n}\r\n\r\n\r\n@mixin a11y-anchor-focus() {\r\n    outline: 2px solid rgba($color-link, 0);\r\n    outline-offset: 4px;\r\n    transition: outline-offset 0.2s ease, color 0.2s ease;\r\n\r\n    &:focus {\r\n        color: $color-link;\r\n        outline-color: rgba($color-link, 0.2);\r\n        outline-offset: 2px;\r\n\r\n        &::before,\r\n        &::after {\r\n            opacity: 0;\r\n        }\r\n    }\r\n}","/* ==========================================================================\r\n\r\n   Base Styles\r\n   ====\r\n\r\n\r\n   Some Basic Styles\r\n   ========================================================================== */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n  -webkit-tap-highlight-color: transparent;\n}\n\nhtml,\nbutton,\ninput,\nselect,\ntextarea {\n  color: #4d4d4d;\n}\n\nhtml {\n  /*\r\n    $font-min: $font-size-default;\r\n    //$font-min: 14px;\r\n    //$font-max: 18px;\r\n    $font-max: 20px;\r\n    $font-diff: ($font-max - $font-min);\r\n    //$breakpoint-min: 1024px;\r\n    $breakpoint-min: 1280px;\r\n    $breakpoint-max: 2560px;\r\n    //$breakpoint-max: 1920px;\r\n    $breakpoint-diff: ($breakpoint-max - $breakpoint-min);\r\n    $font-scale: $font-diff / $breakpoint-diff;\r\n    */\n  font-size: 100%;\n}\n\n@media (min-width: 64em) {\n  html {\n    font-size: calc(0.875em + (2 * (100vw - 1024px) / 256));\n  }\n}\n\n@media (min-width: 80em) {\n  html {\n    font-size: calc(1em + (4 * (100vw - 1280px) / 1280));\n  }\n}\n\nbody {\n  overflow: hidden;\n  min-width: 320px;\n  height: 100vh;\n  margin: 0;\n  padding: 0;\n  background: #fff;\n  color: #4d4d4d;\n  -moz-osx-font-smoothing: grayscale;\n  -webkit-font-smoothing: antialiased;\n  font-family: \"Open Sans\", sans-serif;\n  line-height: 1.25;\n}\n\n::-moz-selection {\n  background-color: rgba(160, 44, 44, 0.2);\n  color: #641c1c;\n  text-shadow: none;\n}\n\n::selection {\n  background-color: rgba(160, 44, 44, 0.2);\n  color: #641c1c;\n  text-shadow: none;\n}\n\n/*\r\n::-webkit-scrollbar {\r\n    width: 8px;\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n    background: darken($color-link, 10%);\r\n}\r\n\r\n::-webkit-scrollbar-track {\r\n    background: $color-link;\r\n}\r\n*/\naudio,\ncanvas,\niframe,\nsvg,\nvideo,\nimg {\n  vertical-align: middle;\n}\n\nhr {\n  display: block;\n  height: 1px;\n  margin: 1rem 0;\n  padding: 0;\n  border: 0;\n  border-top: 1px solid #4d4d4d;\n}\n\nfieldset {\n  margin: 0;\n  padding: 0;\n  border: 0;\n}\n\ntextarea {\n  resize: vertical;\n}\n\ninput::-moz-focus-inner {\n  border: 0;\n}\n\n/* ==========================================================================\r\n   ##prevent 300ms delay in FF, Chrome and IE on touch devices\r\n   ========================================================================== */\na[href],\nbutton {\n  touch-action: manipulation;\n}\n\n/* ==========================================================================\r\n   ##Headings and Base\r\n\r\n   1 to 6 heading\r\n\r\n   ```\r\n   ```\r\n   ========================================================================== */\nh1,\n:global(.h1) {\n  margin: 0 0 0.2em;\n  font-family: \"Bree Serif\", serif;\n  font-size: 2.5rem;\n  font-weight: 400;\n  line-height: 1.2;\n}\n\nh2,\n:global(.h2) {\n  margin: 0 0 0.2em;\n  font-family: \"Bree Serif\", serif;\n  font-size: 2rem;\n  font-weight: 400;\n  line-height: 1.2;\n}\n\nh3,\n:global(.h3) {\n  margin: 0 0 0.2em;\n  font-family: \"Bree Serif\", serif;\n  font-size: 1rem;\n  font-weight: 400;\n  line-height: 1.2;\n}\n\nh4,\n:global(.h4) {\n  margin: 0 0 0.2em;\n  font-family: \"Bree Serif\", serif;\n  font-size: 1rem;\n  font-weight: 400;\n  line-height: 1.2;\n}\n\nh5,\n:global(.h5) {\n  margin: 0 0 0.2em;\n  font-family: \"Bree Serif\", serif;\n  font-size: 1rem;\n  font-weight: 400;\n  line-height: 1.2;\n}\n\nh6,\n:global(.h6) {\n  margin: 0 0 0.2em;\n  font-family: \"Bree Serif\", serif;\n  font-size: 1rem;\n  font-weight: 400;\n  line-height: 1.2;\n}\n\np {\n  margin: 0 0 0.2em;\n  line-height: 1.2;\n}\n\nabbr[title] {\n  border-bottom: 0;\n}\n\n:global(.no-touchevents) abbr[title] {\n  border-bottom: 1px dotted currentColor;\n  text-decoration: none;\n}\n\n@media print {\n  :global(.no-touchevents) abbr[title] {\n    border-bottom: 0;\n  }\n}\